import React from "react"
import { graphql } from "gatsby"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import Heating from "../../components/heating-service"
import GetInTouch from "../../components/forms/get-in-touch"
import i18n from "../../lib/i18n"

export const query = graphql`
  query {
    wpHero(id: { eq: "cG9zdDozMDk=" }) {
      id
      title
      heroFields {
        en {
          headline
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
        es {
          headline
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
      }
    }
  }
`

export default function HeatingPage({ data }) {
  console.log(i18n.language)
  return (
    <Layout>
      <SEO title="Furnace Repair and Installation Heating Experts" />
      <Heating data={data} />
      <GetInTouch
        locationName="Air Rey A/C &amp; Heating"
        // address="5504 Bandera Rd. Suite 202"
        city="Serving Austin, Houston, Dallas, &amp; San Antonio"
        state=""
        zip=""
        phone="469-420-5155"
        email="sales@airreydfw.com"
      />
    </Layout>
  )
}
